import React, {Component} from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Fade from '@material-ui/core/Fade';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import FormLabel from '@material-ui/core/FormLabel';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import LinearProgress from '@material-ui/core/LinearProgress';
import axios from 'axios';

import {
    BrowserRouter as Router,
    Route,
    Link,
    Redirect,
    withRouter
} from "react-router-dom";
import {openSnackbar} from "./Notifier";

const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        textAlign:'center'
    },
    control: {
        padding: theme.spacing.unit * 2,
    },
    button:{
        marginTop: 20
    },
    text:{
        marginTop:10
    }
});

class Register extends Component {
    constructor(props) {
        super(props);

        this.state = {
            spacing: '16',
            loading:false,
            redirect:false,
            name:'',
            username:'',
            email:'',
            password:'',
            ConfirmPassword:''
        }

        this.onSubmit = this.onSubmit.bind(this);
        this.setRedirect = this.setRedirect.bind(this);
        this.renderRedirect = this.renderRedirect.bind(this);
        this.onChangeName = this.onChangeName.bind(this);
        this.onChangeUsername = this.onChangeUsername.bind(this);
        this.onChangeEmail = this.onChangeEmail.bind(this)
        this.onChangePassword = this.onChangePassword.bind(this)
        this.onChangeConfirmPassword = this.onChangeConfirmPassword.bind(this);
    }

    onSubmit(e){
        this.setState({
            loading:true
        })
        e.preventDefault();
        let data = {
            username:this.state.username,
            name:this.state.name,
            email:this.state.email,
            password:this.state.password
        };
        axios.post(`/api/auth/register`, data)
            .then((res)=>{
                this.setState({
                    loading:false
                });
                openSnackbar({ varient:'success',message: res.data.message });
            })
    }

    setRedirect(){
        this.setState({
            redirect:true
        })
    }

    renderRedirect(){
        if(this.state.redirect){
            return <Redirect to='/target' />
        }
    }

    onChangeName(e){
        this.setState({
            name:e.target.value
        })
    }
    onChangeUsername(e){
        this.setState({
            username:e.target.value
        })
    }
    onChangeEmail(e){
        this.setState({
            email:e.target.value
        })
    }
    onChangePassword(e){
        this.setState({
            password:e.target.value
        })
    }
    onChangeConfirmPassword(e){
        this.setState({
            ConfirmPassword:e.target.value
        })
    }
    componentDidMount() {

    }

    render() {
        const { classes } = this.props;
        const { spacing } = this.state;
        return (
            <div className={classes.root}>
                <Grid container spacing={0}>
                    <Grid item  xs={12} sm={4}>

                    </Grid>
                    <Grid item  xs={12} sm={4}>
                        <Typography variant="h4" gutterBottom style={{marginTop:20}}>
                            Register
                        </Typography>
                        <Fade in={this.state.loading}>
                            <LinearProgress  color="secondary" />
                        </Fade>

                        <form onSubmit={this.onSubmit}>
                            <FormControl className={classes.text} fullWidth >
                                <InputLabel htmlFor="name">Name</InputLabel>
                                <Input
                                    id="name"
                                    value={this.state.name}
                                    onChange={this.onChangeName}
                                />
                            </FormControl>
                            <FormControl className={classes.text} fullWidth >
                                <InputLabel htmlFor="username">Username</InputLabel>
                                <Input
                                    id="username"
                                    value={this.state.username}
                                    onChange={this.onChangeUsername}
                                />
                            </FormControl>
                            <FormControl className={classes.text} fullWidth >
                                <InputLabel htmlFor="email">Email</InputLabel>
                                <Input
                                    id="email"
                                    value={this.state.email}
                                    onChange={this.onChangeEmail}
                                />
                            </FormControl>
                            <FormControl  className={classes.text} fullWidth >
                                <InputLabel htmlFor="password">Password</InputLabel>
                                <Input
                                    type="password"
                                    id="password"
                                    value={this.state.password}
                                    onChange={this.onChangePassword}
                                />
                            </FormControl>
                            <FormControl  className={classes.text} fullWidth >
                                <InputLabel htmlFor="password">Re-enter Password</InputLabel>
                                <Input
                                    type="password"
                                    id="password"
                                    value={this.state.ConfirmPassword}
                                    onChange={this.onChangeConfirmPassword}
                                />
                            </FormControl>
                            <Button type="submit" variant="contained" color="primary" fullWidth={true} size='large' className={classes.button}>
                                Register
                            </Button>
                        </form>
                    </Grid>
                    <Grid item  xs={12} sm={4}>

                    </Grid>
                </Grid>
            </div>
        );
    }
}
Register.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(Register);