import React, {Component} from 'react';
import PropTypes from 'prop-types';
import axios from 'axios';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import FormLabel from '@material-ui/core/FormLabel';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';

import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormHelperText from '@material-ui/core/FormHelperText';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import {openSnackbar}  from "./Notifier";
import AsyncButton from "./AsyncButton";



const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    paper: {
        textAlign:'center'
    },
    control: {
        padding: theme.spacing.unit * 2,
    },
    button:{
        marginTop: 20
    },
    text:{
        marginTop:10
    }
});

class Login extends Component {
    constructor(props){
        super(props);

        this.state ={
            spacing: '16',
            username:'',
            password:''
        };
        this.onSubmit = this.onSubmit.bind(this);
        this.onChangeUsername = this.onChangeUsername.bind(this);
        this.onChangePassword = this.onChangePassword.bind(this);
    }

    onSubmit(e){
        e.preventDefault();
        axios.post(`/api/auth/login`, {username:this.state.username, password:this.state.password})
            .then((res)=>{
                console.log(res);
                openSnackbar({ varient:'success',message: res.data.message });
            })
    }
    onChangeUsername(e){
        this.setState({
            username:e.target.value
        })
    }
    onChangePassword(e){
        this.setState({
            password:e.target.value
        })
    }
    render() {
        const { classes } = this.props;
        const { spacing } = this.state;
        return (
            <div>
                <div className={classes.root}>
                    <Grid container spacing={0}>
                        <Grid item xs={12} sm={4}>

                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <Typography variant="h4" gutterBottom style={{marginTop:20}}>
                                Login
                            </Typography>
                            <form onSubmit={this.onSubmit}>
                                <FormControl className={classes.text} fullWidth >
                                    <InputLabel htmlFor="username">Username/Email</InputLabel>
                                    <Input
                                        id="username"
                                        value={this.state.username}
                                        onChange={this.onChangeUsername}
                                    />
                                </FormControl>
                                <FormControl  className={classes.text} fullWidth >
                                    <InputLabel htmlFor="password">Password</InputLabel>
                                    <Input
                                        type="password"
                                        id="password"
                                        value={this.state.password}
                                        onChange={this.onChangePassword}
                                    />
                                </FormControl>
                                <Button type="submit" variant="contained" color="primary" fullWidth={true} size='large' className={classes.button}>
                                    Login
                                </Button>
                                {/*<AsyncButton/>*/}
                            </form>
                        </Grid>
                        <Grid item xs={12} sm={4}>

                        </Grid>
                    </Grid>
                </div>
            </div>
        );
    }
}
Login.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(Login);